export const getScoringDataById = async (id) => {
  const response = await api.get(`/scoring/${id}`);
  return response.data;
};

export const createScoringData = async (formData) => {
  // console.log('formData:', formData);
  // for (let [key, value] of formData.entries()) {
  //   // console.log(`formData ${key}:`, value);
  // }
  const response = await api.post('/scoring', formData, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
  });
  return response.data;
};

export const updateScoringData = async (id, formData) => {
  const response = await api.put(`/scoring/${id}`, formData, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
  });
  return response.data;
};

export const deleteScoringData = async (id) => {
  const response = await api.delete(`/scoring/${id}`);
  return response.data;
}; 

export const calculateScoring = async () => {
  const response = await api.get("/scoring/calculate");
  return response.data;
};

