// ================= CRUD PICTURES =================

// CREATE foto untuk housing tertentu
export const createPicture = async (housingId, formData, onUploadProgress) => {
  const response = await api.post(`/housing/${housingId}/pictures`, formData, {
    headers: { 'Content-Type': 'multipart/form-data' },
    onUploadProgress,
  });
  return response.data;
};

// READ semua foto untuk housing tertentu
export const getPictures = async (housingId) => {
  const response = await api.get(`/housing/${housingId}/pictures`);
  return response.data;
};

// READ satu foto by id
export const getPictureById = async (id) => {
  const response = await api.get(`/housing/pictures/${id}`);
  return response.data;
};

// UPDATE foto (ganti file / deskripsi)
export const updatePicture = async (id, formData) => {
  const response = await api.put(`/housing/pictures/${id}`, formData, {
    headers: { 'Content-Type': 'multipart/form-data' },
  });
  return response.data;
};

// DELETE foto
export const deletePicture = async (id) => {
  const response = await api.delete(`/housing/pictures/${id}`);
  return response.data;
};